.wait {
  margin: 0 0 rem(73);
  @media (max-width: em(767)){
    margin: 0 0 rem(60);
  }
  &__wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  h2 {
    color: $orange;
    margin: 0 0 rem(27);
    &::after {
      bottom: rem(-8);
    }
  }
  p {
    max-width: rem(600);
    text-align: center;
  }
}